import React, { Component } from 'react'
import Fade from 'react-reveal/Fade'
// Link
import { Link } from 'react-router-dom'

export default class AboutBanner extends Component {
  render() {
    return (
      <section id="sec-banner">
        <div className="container">
          <div className="cls-banner-wrapper text-center">
              <Fade bottom> <h2 className="cls-header2">About Us</h2></Fade>
           <Fade bottom> <div className="cls-breadcrumb">
              <ul className="cls-breadcrumb-list">
                <li>
                  <Link to="/">
                    <i className="fas fa-home" />{' '}
                  </Link>
                </li>
                /
                <li>
                  <Link to="/about-us"> About</Link>
                </li>
              </ul>
           </div></Fade>
          </div>
        </div>
      </section>
    )
  }
}



