import React, { Component } from 'react'
import Fade from 'react-reveal/Fade'

export default class HomeBookAppointment extends Component {
  render() {
    return (
      <section id="book-section">
        <div className="container" align="middle">
          <div className="book-title">
              <Fade bottom> <h3>Book Your Appointment</h3></Fade>
        <Fade bottom>    <div className="book-subtitle">
              <span
                style={{ fontSize: '60px' }}
                data-mce-style="font-size: 60px;"
              >
                Call Us Now: <br />
              </span>
        </div></Fade>
             <Fade bottom>
                 <div className="cls-appointment-contact"><a href="tel:+60 16463 7218">+60 16463 7218</a> Wei Loong / <a href="tel:+60 12548 4854">+60 12548 4854</a> Tuck Wai</div>
             </Fade>
          </div>
        </div>
      </section>
    )
  }
}



