import React, { Component } from 'react'
import Fade from 'react-reveal/Fade'
export default class HomeUpdate extends Component {
  render() {
    return (
      <section id="update-section">
        <div className="container" align="middle">
            <Fade bottom>
          <div className="update-title">
            <h3>
                <small>GET UPDATED INFORMATION</small>
              <br />Like Our Facebook Page
            </h3>
          </div></Fade>
            <Fade bottom><div className="all-btn">
            <a
              href="https://www.facebook.com/MalaysiaPropertyInsight155/"
              target="_blank"
            >
              Click Here
            </a>
            </div></Fade>
        </div>
      </section>
    )
  }
}



