import React, { Component } from 'react'
import Fade from 'react-reveal/Fade'
// Const
import { featurePropertyOne, featurePropertyTwo } from '../../../data/const'

// Components
import PropertyCard from '../../../components/PropertyCard'

export default class HomeFeatured extends Component {
  render() {
    return (
      <section id="feature-section">
        <div className="container" align="middle">
            <Fade bottom><h2 className="feature-title">Featured Property</h2></Fade>
            <Fade bottom><p className="feature-subtitle">
            Checkout and enjoy the biggest unlimited possibilities
          </p></Fade>
          <div>
              <Fade bottom><div className="row">
              {featurePropertyOne.map((item, i) => (
                <PropertyCard
                  key={i}
                  link={item.link}
                  image={item.img}
                  name={item.name}
                  details={item.details}
                  price={item.price}
                />
              ))}
            </div></Fade>
              <Fade bottom><div className="row">
              {featurePropertyTwo.map((item, i) => (
                <PropertyCard
                  key={i}
                  link={item.link}
                  image={item.img}
                  name={item.name}
                  details={item.details}
                  price={item.price}
                />
              ))}
            </div></Fade>
          </div>
        </div>
      </section>
    )
  }
}



